import React from 'react';
import Editor from './Editor';
import ClaimInner from './ClaimInner';
import { CSSTransitionGroup } from 'react-transition-group';

function Claim(props) {
    const vm = props.vm;
    return (
        <div className={'claim-outer'}>
            {vm.children.length > 0 &&
                <div className={"expander" + (vm.claim.isChildrenDisplayed ? " expanded" : " collapsed")} onClick={vm.claim.isChildrenDisplayed ? vm.hideChildren : vm.showChildren} >
                	&#9701;
                </div>
            }
            <ClaimInner vm={vm}></ClaimInner>
            {vm.selected &&
                <Editor vm={vm} />
            }
            <ul className="children" style={{
                maxHeight: vm.claim.isChildrenDisplayed ? vm.children.length * 300 + 'px' : '0',
                overflow: vm.claim.isChildrenDisplayed ? 'unset' : 'hidden'
            }}>
                <CSSTransitionGroup
                    transitionName="animate-add-remove"
                    transitionEnterTimeout={500}
                    transitionLeaveTimeout={500}>
                    {vm.children.map((child) => (
                        <Claim key={child.argument.id} vm={child} />
                    ))}
                </CSSTransitionGroup>
            </ul>
        </div>
    );
}

export default Claim;